<template>
  <div style="margin-top:150px">
    <!-- loading,main.jsimport -->
    <loading :active.sync="isLoading"></loading>
    <!-- slider -->
    <carousel :data="data" style="margin-bottom:100px" class="example-slide"></carousel>
    <div class="row">
      <Slide/>
      <div class="col-sm-12 col-md-10">
        <div class="row">
          <!-- card  col ,divclasscarddivclass -->
          <div
            v-for="(item) in filterProductsSearch"
            :key="item.id"
            class="col-sm-12 col-md-4 border-0 item-hover mb-4"
            style="overflow:hidden"
          >
            <!-- PS: tag<img> -->
            <!-- card  col ,divclasscarddivclass -->
            <div
              @click="getProduct(item.id)"
              :style="{ backgroundImage: `url(${item.imageUrl})` }"
              class="photo-scale product-style card"
            ></div>
            <div class="card-body">
              <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
              <h5 class="card-title word-size">
                <a @click.prevent="getProduct(item.id)" href="#" class="text-dark">{{ item.title }}</a>
              </h5>
              <a
                @click.prevent="addFavorite(item)"
                href="#"
                class="float-right"
                style="margin-top:-20px;margin-right:5px"
              >
                <!-- <i class="far fa-star" v-if="!item.isStar"></i> -->
                <!-- <i class="fas fa-star" v-if="item.isStar"></i> -->
                <i class="fa-star" :class="{ 'fas' : item.isStar, 'far' : !item.isStar }"></i>
              </a>
              <p class="card-text">{{ item.description }}</p>
              <p class="card-text">{{ item.content }}</p>
              <div class="justify-content-between align-items-baseline">
                <!-- , -->
                <div class="h5" v-if="!item.origin_price"> {{ item.price | currency }} </div>
                <!--  -->
                <!-- <del> -->
                <div v-else-if="item.origin_price > item.price">
                  <del class="h6"> {{ item.origin_price | currency }} </del>
                  <div class="h5" v-if="item.price"> {{ item.price | currency }} </div>
                </div>
                <div class="h5" v-else> {{ item.price | currency }} </div>
              </div>
            </div>
            <div class="card-footer d-flex" style="padding:10px;border:1px solid gray">
              <button
                @click="getProduct(item.id)"
                type="button"
                class="btn btn-outline-secondary btn-sm"
                style="margin-right:5px"
              >
                <!-- loading,fontawesome PS:icon -->
                <!-- Detail.vue,fontawsomeloading -->
                <!-- <i class="fas fa-spinner fa-spin" v-if="status.loadingItem===item.id"></i>
                -->
                <i class="fas fa-info-circle"></i>
                
              </button>
              <!-- addtoCart()2nd,1 -->
              <button
                @click="addtoCart(item.id)"
                type="button"
                class="btn btn-outline-danger btn-sm ml-auto"
              >
                <!-- fontawesome loading -->
                <!-- <i class="fas fa-spinner fa-spin" v-if="status.loadingItem===item.id"></i> -->
                <i class="fas fa-cart-plus"></i>
                
              </button>
            </div>
          </div>
        </div>
        <!-- <pagination :page-data="pagination" @changepage="getProducts"></pagination> -->
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Slide from "@c/pages/frontEnd/common/Slide";
import { mapState, mapActions, mapGetters } from "vuex";

export default {
  inject: ["reload"],
  components: {
    Slide
  },
  data() {
    return {
      // slider
      data: [
        '<img src="https://i.pinimg.com/originals/9e/91/2f/9e912fc996b37ada8f11a695e8307a40.jpg">',
        '<img src="https://goo.gl/XYij8s">',
        '<img src="https://i.pinimg.com/originals/79/96/4d/79964d1c3fa9b69b152e375364c5586e.jpg">'
      ]
      // searchText: "", // vuex,store
      // clickSlide: false // vuex,store
      // products: [], // vuex,store
      // isLoading: false, // vuex,store
      // product: {}, // vuex,store
      // showCart: [], // vuex,store
      // pagination: {} // vuex,store
      // coupon_code: "" // Cart.vue
      /*
      Detail.vue,fontawsomeloading
      status: {
        loadingItem: "" //id,
      }*/
    };
  },
  methods: {
    addFavorite(item) {
      this.$store.dispatch("productsModules/addFavorite", item);
      // location.reload(); // ,F5,
      this.reload(); // ,App.vueinject
    },
    // store/products.js
    /*
    getProducts(page = 1) {
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const API_PATH = "caris";
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E5%AE%A2%E6%88%B6%E8%B3%BC%E7%89%A9-[%E5%85%8D%E9%A9%97%E8%AD%89]#%E5%8F%96%E5%BE%97%E5%95%86%E5%93%81%E5%88%97%E8%A1%A8
      // ,admin
      const api = `${SERVER_PATH}/api/${API_PATH}/products?page=${page}`;
      const vm = this;

      vm.isLoading = true;

      this.$http.get(api).then(response => {
        vm.products = response.data.products;
        vm.isLoading = false;
        vm.pagination = response.data.pagination;
        // console.log("getProducts", response);
      });
    },*/
    ...mapActions("productsModules", ["getProducts"]),
    // 
    getProduct(id) {
      this.$router.push(`/detail/${id}`);

      // store/products.js
      /*
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const API_PATH = "caris";
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E5%AE%A2%E6%88%B6%E8%B3%BC%E7%89%A9-%5B%E5%85%8D%E9%A9%97%E8%AD%89%5D#%E5%96%AE%E4%B8%80%E5%95%86%E5%93%81%E7%B4%B0%E7%AF%80
      // ,admin
      const api = `${SERVER_PATH}/api/${API_PATH}/product/${id}`;
      const vm = this;

      vm.status.loadingItem = id;

      this.$http.get(api).then(response => {
        // console.log(response.data); //
        vm.product = response.data.product;
        vm.status.loadingItem = "";
        console.log("getProduct", vm.product);

        vm.$bus.$emit("detail", vm.product);
        vm.$router.push(`/detail`);
      });
      */
    },
    addtoCart(id, qty = 1) {
      this.$store.dispatch("cartModules/addtoCart", { id, qty });

      /*
      store
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const API_PATH = "caris";
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E5%AE%A2%E6%88%B6%E8%B3%BC%E7%89%A9-%5B%E5%85%8D%E9%A9%97%E8%AD%89%5D#%E5%8A%A0%E5%85%A5%E8%B3%BC%E7%89%A9%E8%BB%8A
      // ,
      const api = `${SERVER_PATH}/api/${API_PATH}/cart`;
      const vm = this;

      vm.status.loadingItem = id;
      // :data return{}
      const cart = {
        product_id: id,
        qty
      };

      // Products.vuepost(URL,data,callback)
      // post,APIcartdata{},cartAPIdata
      this.$http.post(api, { data: cart }).then(response => {
        // console.log("addtoCart", response.data);
        vm.status.loadingItem = "";
        vm.getCart(); // ,
      });
      */
    },
    // 
    ...mapActions("cartModules", ["getCart"])
    /*
    getCart() {
      store
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const API_PATH = "caris";

      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E5%AE%A2%E6%88%B6%E8%B3%BC%E7%89%A9-%5B%E5%85%8D%E9%A9%97%E8%AD%89%5D#%E5%8F%96%E5%BE%97%E8%B3%BC%E7%89%A9%E8%BB%8A%E5%88%97%E8%A1%A8
      // ,
      const api = `${SERVER_PATH}/api/${API_PATH}/cart`;
      const vm = this;
      vm.isLoading = true;

      this.$http.get(api).then(response => {
        // console.log("getCart(showCart)", response);
        vm.showCart = response.data.data.carts;
        vm.isLoading = false;
      });
    },*/
  },
  computed: {
    // content(vuex)
    filterProductsSearch() {
      const vm = this;

      // 
      if (vm.searchText && !vm.clickSlide) {
        // vm.productsmapGetters
        return vm.products.filter(item => {
          // ,searchproducts,
          // [1, 2, 3].includes(2); // true
          // vuex
          const results = item.title
            .toLowerCase()
            .includes(vm.searchText.toLowerCase());
          return results;
        });
      }

      // slider
      if (vm.searchText && vm.clickSlide) {
        return vm.products.filter(item => {
          // const check = item.category == vm.clickSlide; // 
          // console.log("clickSlide", check);
          // console.log("clickSlide2", item.category);
          return item.category === vm.searchText;
        });
      }

      return vm.products; // ,
    },
    ...mapGetters("productsModules", [
      "products",
      "pagination",
      "searchText",
      "clickSlide"
    ]),
    ...mapGetters("cartModules", ["isLoading", "cart"])
  },
  created() {
    const vm = this;
    vm.getProducts();
  }
};
</script>
<style lang="scss" scoped>
// slider
.example-slide {
  align-items: center;
  background-color: #666;
  color: #999;
  display: flex;
  font-size: 1.5rem;
  justify-content: center;
  min-height: 10rem;
}
.item-hover:hover {
  background-color: darken(white, 10%);
  .photo-scale {
    animation-name: photo_scale;
    animation-duration: 1s;
  }
}
.product-style {
  height: 230px;
  background-size: cover;
  background-position: center;
  cursor: pointer;
}
.word-size {
  white-space: nowrap; // 
  text-overflow: ellipsis; //
  overflow: hidden; // 
}
@keyframes photo_scale {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(1.1, 1.1);
  }
}
</style>



