<template>
  <div>
    <!-- v-model(form) value, checked, selectedv-model="true"checked -->
    <loading :active.sync="isLoading"></loading>
    <button class="btn btn-outline-info mt-3" @click="openModel('')"></button>
    <table class="table table-hover mt-4">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"> % </th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"> / </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons" :key="item.id" class="item-hover">
          <th scope="row">{{ item.title }}</th>
          <td>{{ item.code }}</td>
          <td>{{ item.percent }}</td>
          <!-- filterstimestampdue_date, main.js -->
          <td>{{ item.due_date | timestamp }}</td>
          <td>
            <b class="text-success" v-if="item.is_enabled"></b>
            <b class="text-danger" v-else></b>
          </td>
          <td>
            <button class="btn btn-outline-primary mr-1" @click="openModel('', item)"></button>
            <!-- id -->
            <button class="btn btn-outline-secondary" @click="deletCoupon(item.id)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- / -->
    <div class="modal fade" id="modal-add" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered modal-lg" role="document">
        <form class="modal-content" @submit.prevent="updateCoupon()">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle">{{openChoice}}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12 mt-2">
                <label></label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model="tempData.title"
                  v-validate="'required'"
                  name="coupon_name"
                >
                <span class="text-danger" v-if="errors.has('coupon_name')"></span>
              </div>
              <div class="col-md-6 mt-2">
                <label>(%)</label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=" 80 "
                  v-model="tempData.percent"
                  v-validate="'required'"
                  name="coupon_percent"
                >
                <span class="text-danger" v-if="errors.has('coupon_percent')"></span>
              </div>
              <div class="col-md-6 mt-2">
                <div class="form-group">
                  <input
                    type="text"
                    name="tt"
                    id
                    v-validate="'required|date_format:YYYY/MM/DD|after:2015/01/01'"
                  >
                  <span class="text-danger" v-if="errors.has('tt')">ttt</span>
                  <label></label>
                  <!-- :class:disabled -->
                  <input
                    type="date"
                    class="form-control"
                    :class="{'is-invalid': errors.has('end-date')}"
                    :disabled="!tempData.is_enabled && openChoice===''"
                    name="end-date"
                    v-model="due_date"
                    v-validate="'required|date_format:YYYY/MM/DD|after:2015/01/01'"
                  >
                  <span
                    v-if="!tempData.is_enabled && openChoice===''"
                    class="text-primary"
                  ></span>
                  <span class="text-danger" v-if="errors.has('end-date')"></span>
                </div>
              </div>
              <div class="col-md-12 mt-2">
                <div class="form-group">
                  <label for="coupon-code"></label>
                  <input
                    type="text"
                    class="form-control"
                    :class="{'is-invalid': errors.has('coupon-code')}"
                    name="coupon-code"
                    id="coupon-code"
                    placeholder=""
                    v-model="tempData.code"
                    v-validate="'required'"
                  >
                  <span class="text-danger" v-if="errors.has('coupon-code')"></span>
                </div>
              </div>
              <div class="col-md-12 mt-3">
                <label>
                  <input
                    v-if="openChoice===''"
                    :disabled="checked"
                    v-model="checked"
                    type="checkbox"
                    value=""
                  >
                  <input v-else type="checkbox" value="" v-model="tempData.is_enabled">
                  <span v-if="openChoice===''">()</span>
                  <span v-else>/</span>
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="submit" class="btn btn-primary"></button>
          </div>
        </form>
      </div>
    </div>
    <!--  -->
    <!-- <pagination>main.jsVue.component('pagination', Pages) -->
    <!--  -->
    <!-- :page-data="pagination"Pagination.vuepageDatapagination{} -->
    <pagination :page-data="pagination" @changepage="getCoupon"></pagination>
    <!-- $emit("changepage", page) -->
  </div>
</template>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      checked: true, // 
      due_date: "", // 
      isLoading: false,
      openChoice: "", // updateCouponAPImethod
      coupons: [],
      pagination: {},
      // (put)(delete)this.tempData = Object.assign({}, item);item{},item & tempData
      // API
      tempData: {
        title: "",
        percent: 100,
        code: ""
      }
    };
  },
  methods: {
    onchange() {
      // jsonchange,:html@change="onchange",
    },
    getCoupon(page = 1) {
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E5%8F%96%E5%BE%97%E5%84%AA%E6%83%A0%E5%88%B8%E5%88%97%E8%A1%A8
      // 
      const api = `${process.env.SERVER_API_PATH}/api/${
        process.env.USER_PATH
      }/admin/coupons?page=${page}`;
      const vm = this;

      vm.isLoading = true;

      this.$http.get(api).then(response => {
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
        console.log("getCoupon", response);
      });
    },
    openModel(openChoice, item) {
      // 
      const vm = this;
      if (openChoice === "") {
        vm.tempData = {}; // 
        vm.due_date = "";
        vm.openChoice = ""; // updateCouponAPImethod
        vm.checked = true;
        vm.tempData.is_enabled = vm.checked;
      } else {
        // 
        vm.due_date = new Date(item.due_date * 1000)
          .toISOString()
          .split("T")[0];
        // Tdate & time,

        // console.log(
        //   "before split",
        //   new Date(item.due_date * 1000).toISOString()
        // );
        // console.log("after split", vm.due_date);

        // openModelvm.tempData(watchvm.tempData.due_date),updateCouponAPI
        // https://jigsawye.com/2015/10/06/javascript-object-assign/
        vm.tempData = Object.assign({}, item); // 
        // ,tempDataopenChoice=''tempData,ES6
        // item{}vm.tempData,item & tempData

        vm.openChoice = ""; // openChoice='' (),updateCoupon
      }
      $("#modal-add").modal("show");
    },
    updateCoupon() {
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E6%96%B0%E5%A2%9E%E5%84%AA%E6%83%A0%E5%88%B8
      // 
      let api = `${process.env.SERVER_API_PATH}/api/${
        process.env.USER_PATH
      }/admin/coupon`;
      let httpMethods = "post";
      const vm = this;

      // api()
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E4%BF%AE%E6%94%B9%E5%84%AA%E6%83%A0%E5%88%B8
      if (vm.openChoice === "") {
        //apiconsterror,var or let
        api = `${process.env.SERVER_API_PATH}/api/${
          process.env.USER_PATH
        }/admin/coupon/${vm.tempData.id}`;
        httpMethods = "put";
      }

      vm.isLoading = true;
      // console.log("updateCoupon-vm.tempData", vm.tempData); //openModel

      // ,
      this.$validator.validate().then(result => {
        // (id)
        if (result) {
          // ,{}data:{},data:{}
          // vm.tempData/admin/coupon/data()
          // /admin/coupon/:id/data()
          this.$http[httpMethods](api, { data: vm.tempData }).then(response => {
            if (response.data.success) {
              // 
              $("#modal-add").modal("hide");
              vm.getCoupon();
              // console.log("$validator-success", response);
            } else {
              $("#modal-add").modal("hide");
              vm.getCoupon();
              console.log("", response.data);
            }
            vm.isLoading = false;
          });
        } else {
          console.log("");
          vm.isLoading = false;
        }
      });
    },
    deletCoupon(coupon_id) {
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E5%88%AA%E9%99%A4%E5%84%AA%E6%83%A0%E5%88%B8
      // 
      let api = `${process.env.SERVER_API_PATH}/api/${
        process.env.USER_PATH
      }/admin/coupon/${coupon_id}`;
      const vm = this;
      const d = confirm("?");

      if (d) {
        this.$http.delete(api).then(response => {
          vm.getCoupon();
          console.log("deletCoupon", response.data);
        });
      }
    }
  },
  watch: {
    // due_data,
    due_date() {
      const vm = this;
      // updateCoupon()
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      // tempDatadue_date
      vm.tempData.due_date = timestamp; // openModelvm.tempData = Object.assign({}, item),updateCouponAPI
    },
    // 
    "tempData.is_enabled": function() {
      const vm = this;
      if (vm.openChoice === "" && !vm.tempData.is_enabled) {
        // 
        const nowDate = new Date(new Date().toLocaleDateString()); //
        const preDate = new Date(nowDate.getTime() - 24 * 60 * 60 * 1000 * 2); // 
        vm.due_date = new Date(preDate).toISOString().split("T")[0]; // watchdue_date
      }
    }
  },
  created() {
    this.getCoupon();

    /* 
    const cur_timestamp = Math.floor(new Date() / 1000);
    console.log("", cur_timestamp);
    const return_timestamp = new Date(
      cur_timestamp * 1000
    ).toLocaleDateString();
    console.log("", return_timestamp);

    var nowDate = new Date(new Date().toLocaleDateString()); //
    var preDate = new Date(nowDate.getTime() - 24 * 60 * 60 * 1000);
    console.log("test", nowDate, preDate.toLocaleDateString());
    */
  }
};
</script>
<style lang="scss" scoped>
.item-hover:hover {
  background-color: darken(white, 10%);
}
</style>


