<template>
  <div>
    <!-- https://github.com/ankurk91/vue-loading-overlay Usage -->
    <!-- ,isLoading==true() -->
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <!-- https://getbootstrap.com/docs/4.2/components/modal/ -->
      <!-- Live demo data-toggle="modal" data-target="#exampleModal"button,Modal -->
      <!-- Button trigger modal -->
      <!--<button
      type="button"
      class="btn btn-primary mt-4"
      data-toggle="modal"
      data-target="#productModal"
      ></button>-->
      <!-- BS4data-toggle="modal"vuejs$("#productModal").modal("show");       -->
      <button type="button" class="btn btn-primary mt-4" @click="openModal(true)"></button>
    </div>
    <!-- table,<td><th> -->
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <!-- https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E5%8F%96%E5%BE%97%E5%95%86%E5%93%81%E5%88%97%E8%A1%A8 -->
        <!--  -->
        <tr v-for="(item, index) in products_pagination" :key="index" class="item-hover">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <!-- currency,filters -->
          <td>{{item.origin_price | currency}}</td>
          <td>{{item.price | currency}}</td>
          <td>
            <!-- :is_enabled == 1 -->
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <div class="btn-group" role="group" aria-label="Basic example">
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)"></button>
              <button class="btn btn-outline-danger btn-sm" @click="delModal(item)"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- Pagination.vuepagegetProducts -->
    <pagination :page-data="pagination" @changepage="getProducts"></pagination>

    <!-- () -->
    <!-- https://getbootstrap.com/docs/4.2/components/pagination/ -->
    <!--  -->
    <!--
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li :class="{ 'disabled':!products_pagination.has_pre }" class="page-item">
          <a
            @click.prevent="getProducts(products_pagination.current_page-1)"
            class="page-link"
            href="#"
            aria-label="Previous"
          >
            <span aria-hidden="true">&laquo;</span>
          </a>
    </li>-->
    <!-- console products_pagination.total_pages PS:(10)-->
    <!-- APIJSON,pagination, -->
    <!--
        <li
          class="page-item"
          v-for="(page) in products_pagination.total_pages"
          :key="page"
          :class="{ 'active':products_pagination.current_page === page }"
        >
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
        </li>
        <li :class="{ 'disabled':!products_pagination.has_next }" class="page-item">
          <a
            @click.prevent="getProducts(products_pagination.current_page+1)"
            class="page-link"
            href="#"
            aria-label="Next"
          >
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>-->

    <!-- Modal, -->
    <!-- https://github.com/hexschool/vue-course-api-wiki/wiki/%E8%AA%B2%E7%A8%8B%E9%83%A8%E5%88%86%E6%A8%A1%E6%9D%BF -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    v-model="tempProduct.imageUrl"
                    type="text"
                    class="form-control"
                    id="image"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUpLoading"></i>
                  </label>
                  <!-- 92 -->
                  <input
                    @change="uploadFile"
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                  >
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  alt
                  :src="tempProduct.imageUrl"
                >
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    v-model="tempProduct.title"
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder=""
                  >
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      v-model="tempProduct.category"
                      type="text"
                      class="form-control"
                      id="category"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      v-model="tempProduct.unit"
                      type="unit"
                      class="form-control"
                      id="unit"
                      placeholder=""
                    >
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      v-model="tempProduct.origin_price"
                      type="number"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                      name="original_price"
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      v-model="tempProduct.price"
                      type="number"
                      class="form-control"
                      id="price"
                      placeholder=""
                      name="price"
                      v-validate="'required'"
                    >
                    <span class="text-danger" v-if="errors.has('price')"></span>
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    v-model="tempProduct.description"
                    type="text"
                    class="form-control"
                    id="description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    v-model="tempProduct.content"
                    type="text"
                    class="form-control"
                    id="content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                    >
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button @click="updateProduct" type="button" class="btn btn-primary"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button @click="delProduct" type="button" class="btn btn-danger"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// vscodechrome console$,
import $ from "jquery";
import { mapState, mapActions, mapGetters } from "vuex";

export default {
  data() {
    return {
      // products: [], // (get),,
      tempProduct: {}, // (put)(delete)this.tempProduct = Object.assign({}, item);item{},item & tempProduct
      isNew: false, // 
      // isLoading: false,
      status: {
        fileUpLoading: false //Loading,
      }
    };
  },
  methods: {
    // ,(get),admin
    // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E5%8F%96%E5%BE%97%E5%95%86%E5%93%81%E5%88%97%E8%A1%A8
    getProducts(page = 1) {
      // store/products.js
      this.$store.dispatch("productsModules/getProducts", page);
      /*
      // 95(page = 1 )
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const API_PATH = "caris";
      const api = `${SERVER_PATH}/api/${API_PATH}/admin/products?page=${page}`; // 95?page=${page}
      const vm = this;

      vm.isLoading = true; //AJAXLoading

      this.$http.get(api).then(response => {
        // console.log("getProducts", response.data);
        vm.products = response.data.products;
        vm.isLoading = false; //Loading
        vm.pagination = response.data.pagination;
      });*/
    },
    // BS4data-toggle="modal"vuejs$("#productModal").modal("show");
    // ajax,,BS4,,vuejs
    // ,
    openModal(addNew, item) {
      if (addNew) {
        // 
        this.tempProduct = {};
        this.isNew = true;
      } else {
        // 
        // ,tempProductaddNew==truetempProduct,ES6
        // item{}this.tempProduct,item & tempProduct
        this.tempProduct = Object.assign({}, item);
        // https://jigsawye.com/2015/10/06/javascript-object-assign/

        this.isNew = false;
        // console.log("openModal-false", this.tempProduct); // 
      }

      // https://getbootstrap.com/docs/4.2/components/modal/#modalshow
      $("#productModal").modal("show");
    },
    // (post)(put),admin
    updateProduct() {
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-[%E9%9C%80%E9%A9%97%E8%AD%89]#%E5%95%86%E5%93%81%E5%BB%BA%E7%AB%8B
      // 
      let SERVER_PATH = "https://vue-course-api.hexschool.io";
      let API_PATH = "caris";
      let api = `${SERVER_PATH}/api/${API_PATH}/admin/product`;
      let httpMethod = "post";
      const vm = this;

      // api
      if (!vm.isNew) {
        // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E4%BF%AE%E6%94%B9%E7%94%A2%E5%93%81
        // ,id,put,id
        api = `${SERVER_PATH}/api/${API_PATH}/admin/product/${
          vm.tempProduct.id
        }`;
        httpMethod = "put";
      }

      // ,
      this.$validator.validate().then(result => {
        // 
        if (result) {
          /**
      $.post(URL,data,callback);
       URL  URL
       data ()
       callback 
      */
          // ,{}data:{},data:{}
          // vm.tempProduct/admin/product/data()
          // admin/product/:id/data()
          this.$http[httpMethod](api, { data: vm.tempProduct }).then(
            response => {
              // 91this.$http.post()[]method
              // console.log("products-post", response.data);
              if (response.data.success) {
                // 
                $("#productModal").modal("hide");
                // 
                vm.getProducts();
              } else {
                $("#productModal").modal("hide");
                vm.getProducts();
                console.log("");
              }
            }
          );
        } else {
          console.log("");
          this.$bus.$emit("msg-from-products", "", "danger");
        }
      });
    },
    delModal(item) {
      this.tempProduct = Object.assign({}, item);
      $("#delProductModal").modal("show");
    },
    // (delete),admin,methodsdelete
    delProduct() {
      let SERVER_PATH = "https://vue-course-api.hexschool.io";
      let API_PATH = "caris";
      const vm = this;
      let api = `${SERVER_PATH}/api/${API_PATH}/admin/product/${
        vm.tempProduct.id
      }`;

      this.$http.delete(api).then(response => {
        // console.log("products-delete", response.data);
        if (response.data.success) {
          $("#delProductModal").modal("hide");
          vm.getProducts();
        } else {
          $("#delProductModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    },
    // admin,methodspost
    uploadFile() {
      // console.log("uploadFile-this", this);
      // VueComponent/$refs/$files/files => JSON
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const api = `${SERVER_PATH}/api/caris/admin/upload`;

      vm.status.fileUpLoading = true;

      // https://openhome.cc/Gossip/ECMAScript/FormData.html
      const formData = new FormData(); //
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E4%B8%8A%E5%82%B3%E5%9C%96%E7%89%87
      // file-to-upload(/<input type="file" name="file-to-upload">),phpform data,
      formData.append("file-to-upload", uploadedFile);

      // 
      // formdata(/enctype)
      // http://www.cnblogs.com/wenzheshen/p/5753693.html
      this.$http
        .post(api, formData, {
          header: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(response => {
          // console.log("uploadFile-response.data", response.data);
          vm.status.fileUpLoading = false;
          if (response.data.success) {
            //1,,,vue devtool,
            // vm.tempProduct.imageUrl = response.data.imageUrl;
            // console.log("uploadFile-response-tempProduct", vm.tempProduct);
            //idnumtitle(...),,imageUrl

            // :(imageUrl)
            // vm.$set(object, key, value),
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
            // console.log("uploadFile-$set", vm.tempProduct); //imageUrl(...)
          } else {
            // main.js import './bus'
            // created()
            // $emit(),2,AlertMessage.vue$on
            this.$bus.$emit(
              "msg-from-products",
              response.data.message,
              "danger"
            );
          }
        });
    }
  },
  computed: {
    ...mapGetters("productsModules", ["products_pagination", "pagination"]),
    ...mapGetters("cartModules", ["isLoading"])
  },
  created() {
    // AJAXcreated
    this.getProducts();
  }
};
</script>
<style lang="scss" scoped>
table.table {
  text-align: center;
}
.item-hover:hover {
  background-color: darken(white, 10%);
}
</style>


