// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// 
import Vue from 'vue'
import App from './App'
import router from './router'

//  @src
import './bus' // 
import currencyFilter from './filters/currency' //
import timestampFilter from './filters/timestamp'; //
import Pages from '@c/commons/Pagination' // 
import Alert from '@c/commons/AlertMessage' // Alert
import store from './store'

// 
import axios from 'axios'
import VueAxios from 'vue-axios' // axiosAJAXVue,this.$http
import Loading from 'vue-loading-overlay'; // Loading
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap' // jquerypopper
import VeeValidate from 'vee-validate' // ,
// https://www.udemy.com/vue-hexschool/learn/v4/t/lecture/14052221?start=0
import zhTWvalidate from 'vee-validate/dist/locale/zh_TW' // 
import Vuex from 'vuex'
import Vtip from 'vtip' // tooltip
import 'vtip/lib/index.min.css'
import VueCarousel from '@chenfengyuan/vue-carousel'; // slider

// ()
Vue.use(Vuex)
Vue.use(VueAxios, axios)
Vue.use(VeeValidate)
VeeValidate.Validator.localize('zh_TW', zhTWvalidate) // vue.use(),
Vue.use(Vtip.directive)
Vue.prototype.$tip = Vtip.tip

// 
Vue.filter('timestamp', timestampFilter);
Vue.filter('currency', currencyFilter) //
Vue.component('pagination', Pages); // 
Vue.component('Alert', Alert); // Alert

// 
Vue.component('Loading', Loading) // Loading(fontawesome)
Vue.component(VueCarousel.name, VueCarousel);

Vue.config.productionTip = false // 
axios.defaults.withCredentials = true;
// ,sessioncookies,API,,,91

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store, // components
  components: {
    App,
  },
  template: '<App/>'
})

// !!!!!!
// PS:
// https://router.vuejs.org/zh/guide/advanced/navigation-guards.html#%E5%85%A8%E5%B1%80%E5%89%8D%E7%BD%AE%E5%AE%88%E5%8D%AB
router.beforeEach((to, from, next) => {
  // console.log('main.js/to', to, 'main.js/from', from, 'main.js/next', next);
  // console.log('main.js/meta:', to.meta);

  // router/index.js,meta,if()to.meta,,
  // PSmeta.requiresAuth,
  if (to.meta.requiresAuth) {
    // ,82
    // const SERVER_PATH = "https://vue-course-api.hexschool.io";
    // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%99%BB%E5%85%A5%E5%8F%8A%E9%A9%97%E8%AD%89#%E6%AA%A2%E6%9F%A5%E7%94%A8%E6%88%B6%E6%98%AF%E5%90%A6%E4%BB%8D%E6%8C%81%E7%BA%8C%E7%99%BB%E5%85%A5
    // 
    const api = `${process.env.SERVER_API_PATH}/api/user/check`; // api

    // router.beforeEach(),vue,this.$http,axios
    axios.post(api).then(response => {
      // console.log('main.js-requiresAuth', response);
      // console.log('main.js-requiresAuth-success', response.data.success);
      // ,success=false

      if (response.data.success) {
        // ,
        next()
      } else {
        // 
        next({
          path: '/login'
        })
      }
    });
  } else {
    // 
    next()
  }
})



