import Vue from 'vue'
import Router from 'vue-router'

// 
import Login from '@c/pages/frontEnd/Login'
import Home from '@c/pages/frontEnd/Home'
import Cart from '@c/pages/frontEnd/Cart'
import CustomerCheckout from '@c/pages/frontEnd/CustomerCheckout'
import FrontTemp from '@c/pages/FrontEnd/common/Front_Temp'
import Detail from '@c/pages/FrontEnd/Detail'
import Favorite from '@c/pages/FrontEnd/Favorite'

// 
import Orders from '@c/pages/backEnd/Orders'
import Products from '@c/pages/backEnd/Products'
import Coupons from '@c/pages/backEnd/Coupons'
import BackTemp from '@c/pages/backEnd/common/Back_Temp'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '*', // ,login
      redirect: 'login'
    },
    {
      path: '/',
      name: '',
      component: FrontTemp,
      children: [{
        path: 'login',
        name: 'Login',
        component: Login,
      }, {
        path: '',
        name: 'Home',
        component: Home,
      }, {
        path: 'favorite',
        name: 'Favorite',
        component: Favorite,
      }, {
        path: 'cart',
        name: 'Cart',
        component: Cart,
      }, {
        path: 'detail/:detailId',
        name: 'Detail',
        component: Detail,
      }, {
        path: 'customer_checkout/:orderId',
        name: 'CustomerCheckout',
        component: CustomerCheckout,
      }, ]
    },
    {
      // admin
      path: '/admin',
      name: 'admin',
      component: BackTemp,
      // childrenpath/
      children: [{
        path: 'products',
        name: 'Products',
        component: Products,
        // https://router.vuejs.org/zh/guide/advanced/meta.html
        meta: {
          // ,/products(main.js)
          // console,,meta={} PS:(main.jsrouter.beforeEach())
          // ,,metashow
          requiresAuth: true
        },
      }, {
        path: 'orders',
        name: 'Orders',
        component: Orders,
        meta: {
          requiresAuth: true
        },
      }, {
        path: 'coupons',
        name: 'Coupons',
        component: Coupons,
        meta: {
          requiresAuth: true
        },
      }]
    },
  ]
})



