import axios from 'axios'

export default {
  namespaced: true, // actions, mutations, getters
  // console
  strict: true,
  state: {
    isLoading: false, //ajax,
    // API
    cart: {
      carts: []
    },
  },
  actions: {
    // context:
    // status:,dispatch()
    // mapActions

    // Cart.vueuseCouponCodecreateOrder
    updateLoading(context, status) {
      // commit('',)mutations
      context.commit('LOADING', status)
      // LOADINGmutations,root
    },
    getCart(context) {
      const api = `${process.env.SERVER_API_PATH}/api/${process.env.USER_PATH}/cart`;

      context.commit('LOADING', true)

      axios.get(api).then(response => {
        if (response.data.data.carts) {
          context.commit('CART', response.data.data)
          console.log("", response);
        }
        context.commit('LOADING', false)
      });
    },
    addtoCart(context, {
      id,
      qty
    }) {
      const api = `${process.env.SERVER_API_PATH}/api/${process.env.USER_PATH}/cart`;
      const addItem = {
        product_id: id,
        qty
      };

      context.commit('LOADING', true)

      axios.post(api, {
        data: addItem
      }).then(response => {
        // LOADINGmutations,rootModules
        context.commit('LOADING', false)
        //,navbar:this.$store.dispatch()
        context.dispatch('getCart')
        console.log(":", response);
      });
    },
    removeCartItem(context, {
      id,
      title
    }) {
      const api = `${process.env.SERVER_API_PATH}/api/${process.env.USER_PATH}/cart/${id}`;
      var r = confirm(`${title}?`);

      context.commit('LOADING', true)

      if (r) {
        axios.delete(api).then(response => {
          console.log("removeCartItem", response);
          context.dispatch('getCart')
          context.commit('LOADING', false)
        });
      } else {
        context.commit('LOADING', false)
      }
    },
  },
  mutations: {
    // statestate
    // status(payload):,actionscommit()
    LOADING(state, status) {
      state.isLoading = status
    },
    CART(state, payload) {
      state.cart = payload
    },
  },
  getters: {
    /*
    isLoading(state) {
      return state.isLoading;
    },
    */
    isLoading: state => state.isLoading,
    cart: state => state.cart,
  }
}



